import React, { memo } from "react";

function PasswordConfirmation() {
    let [confirm, setConfirm] = React.useState("")

    React.useEffect(()=>{
    setConfirm("Successfully changed");
    },[])
    
    return (
        <div className="text-center text-success">{confirm}</div>
    )
}
export default memo(PasswordConfirmation)
